import { createUserWithEmailAndPassword } from "firebase/auth";
import {
  addDoc,
  collection,
  doc,
  serverTimestamp,
  setDoc,
} from "firebase/firestore";
import {
  deleteObject,
  getDownloadURL,
  getStorage,
  ref,
  uploadBytesResumable,
} from "firebase/storage";
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { toast } from "react-toastify";
import slugify from "slugify";
import Button from "../../components/button/Button";
import Radio from "../../components/checkbox/Radio";
import Field from "../../components/field/Field";
import FieldCheckboxes from "../../components/field/FieldCheckboxes";
import Input from "../../components/input/Input";
import Label from "../../components/label/Label";
import ImageUpload from "../../components/uploadimage/ImageUpload";
import { auth, db } from "../../firebase/firebaseConfig";
import { userRole, userStatus } from "../../utils/constants";
import DashboardHeading from "../dashboard/DashboardHeading";

const UserAddNew = () => {
  const {
    control,
    watch,
    handleSubmit,
    reset,
    setValue,
    getValues,
    formState: { errors, isSubmitting, isValid },
  } = useForm({
    mode: "onChange",
    defaultValues: {
      fullname: "",
      username: "",
      email: "",
      password: "",
      status: 1,
      role: 3,
      image_name: "",
      image_url: "",
      createdAt: new Date(),
    },
  });

  const [url, setUrl] = useState("");
  const [progress, setProgress] = useState(0);
  const storage = getStorage();

  const watchStatus = watch("status");
  const watchRole = watch("role");

  const handleAddNewUser = async (values) => {
    const cloneValues = { ...values };
    cloneValues.username = slugify(
      cloneValues.fullname || cloneValues.username,
      {
        lower: true,
        replacement: "",
      }
    );
    try {
      await createUserWithEmailAndPassword(
        auth,
        cloneValues.email,
        cloneValues.password
      );
      await setDoc(doc(db, "users", auth.currentUser.uid), {
        fullname: cloneValues.fullname,
        username: cloneValues.username,
        email: cloneValues.email,
        password: cloneValues.password,
        status: Number(cloneValues.status),
        role: Number(cloneValues.role),
        image_name: cloneValues.image_name,
        avatar: cloneValues.image_url,
        createdAt: serverTimestamp(),
      });
      console.log(cloneValues);
      toast.success(`Successfully add new user email : ${cloneValues.email}`, {
        pauseOnHover: false,
        autoClose: 1000,
      });
    } catch (error) {
      console.log(error);
    } finally {
      reset({
        fullname: "",
        username: "",
        email: "",
        password: "",
        status: 1,
        role: 3,
        image_name: "",
        image_url: "",
        createdAt: new Date(),
      });
      setUrl("");
      setProgress(0);
    }
  };
  const handleUploadImage = (file) => {
    const storage = getStorage();
    const storageRef = ref(storage, "images/" + file.name);
    const uploadTask = uploadBytesResumable(storageRef, file);

    uploadTask.on(
      "state_changed",
      (snapshot) => {
        const progressBar =
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        setProgress(progressBar);

        switch (snapshot.state) {
          case "paused":
            break;
          case "running":
            break;
          default:
        }
      },
      (error) => {
        console.log(error);
      },
      () => {
        getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
          setUrl(downloadURL);
          setValue("image_url", downloadURL);
        });
      }
    );
  };

  const handleGetImage = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    setValue("image_name", file.name);
    handleUploadImage(file);
  };

  const handleDelete = () => {
    const desertRef = ref(storage, "images/" + getValues("image_name"));

    deleteObject(desertRef)
      .then(() => {
        setUrl("");
        setProgress(0);
        console.log("succes");
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <div>
      <DashboardHeading title="New user" desc="Add new user to system">
        <Button type="button" to="/manage/user">
          Back to user manage
        </Button>
      </DashboardHeading>
      <form onSubmit={handleSubmit(handleAddNewUser)}>
        <Field>
          <ImageUpload
            handleDelete={handleDelete}
            image={url}
            progress={progress}
            className="h-[200px]"
            onChange={handleGetImage}
          ></ImageUpload>
        </Field>
        <div className="form-layout">
          <Field>
            <Label htmlFor="fullname">Fullname</Label>
            <Input
              name="fullname"
              placeholder="Enter your fullname"
              control={control}
            ></Input>
          </Field>
          <Field>
            <Label htmlFor="username">Username</Label>
            <Input
              name="username"
              placeholder="Enter your username"
              control={control}
            ></Input>
          </Field>
        </div>
        <div className="form-layout">
          <Field>
            <Label htmlFor="email">Email</Label>
            <Input
              name="email"
              placeholder="Enter your email"
              control={control}
              type="email"
            ></Input>
          </Field>
          <Field>
            <Label htmlFor="password">Password</Label>
            <Input
              name="password"
              placeholder="Enter your password"
              control={control}
              type="password"
            ></Input>
          </Field>
        </div>
        <div className="form-layout">
          <Field>
            <Label htmlFor="status">Status</Label>
            <FieldCheckboxes>
              <Radio
                name="status"
                control={control}
                checked={Number(watchStatus) === userStatus.ACTIVE}
                value={userStatus.ACTIVE}
              >
                Active
              </Radio>
              <Radio
                name="status"
                control={control}
                checked={Number(watchStatus) === userStatus.PENDING}
                value={userStatus.PENDING}
              >
                Pending
              </Radio>
              <Radio
                name="status"
                control={control}
                checked={Number(watchStatus) === userStatus.BAN}
                value={userStatus.BAN}
              >
                Banned
              </Radio>
            </FieldCheckboxes>
          </Field>
          <Field>
            <Label htmlFor="role">Role</Label>
            <FieldCheckboxes>
              <Radio
                name="role"
                control={control}
                checked={Number(watchRole) === userRole.ADMIN}
                value={userRole.ADMIN}
              >
                Admin
              </Radio>
              <Radio
                name="role"
                control={control}
                checked={Number(watchRole) === userRole.MOD}
                value={userRole.MOD}
              >
                Moderator
              </Radio>

              <Radio
                name="role"
                control={control}
                checked={Number(watchRole) === userRole.USER}
                value={userRole.USER}
              >
                User
              </Radio>
            </FieldCheckboxes>
          </Field>
        </div>
        <Button
          kind="primary"
          className="mx-auto w-[200px]"
          type="submit"
          disabled={isSubmitting}
          isLoading={isSubmitting}
        >
          Add new user
        </Button>
      </form>
    </div>
  );
};

export default UserAddNew;
